body {
  background-color: black;
  text-align: center;
}
h2 {
  color: red;
}
.App {
  margin: 50px auto;
  padding-bottom: 50px;
  text-align: center;
  width: 60%;
  border: 1px solid rgb(192, 189, 189);
  -webkit-box-shadow: 0px 14px 70px 10px rgba(170, 170, 170, 1);
  -moz-box-shadow: 0px 14px 70px 10px rgba(170, 170, 170, 1);
  box-shadow: 0px 14px 70px 10px rgba(170, 170, 170, 1);
  background: rgb(249, 255, 94);
  background: linear-gradient(
    158deg,
    rgba(249, 255, 94, 1) 0%,
    rgba(19, 190, 3, 1) 64%
  );
}

.resultsComp {
  width: 90%;
  margin: 15px auto;
}

.ulApp {
  margin-right: 40px;
}

.liApp {
  border: 1px solid #232323;
  list-style-type: none;
  margin: 10px;
  padding: 20px;
}

.venue-name {
  font-size: 20px;
}
